.loginWrapper{
    width: 100%;
    background: url('../../assets/img/wave.png') no-repeat;
    height: 100vh;
    background-size: 100% 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

.formLogin{
    width: 34%;
    max-width: 70%;
    border-radius: 24px;
    background: var(--ffffff, #FFF);
    display: flex;
    flex-direction: column;
    padding: 24px;
    align-items: center;
    gap: 1vh;
}

@media screen and (max-width: 1024px) {
    .formLogin {
        width: 50%; /* При уменьшении ширины экрана устанавливаем ширину до 80% */
    }
}

@media screen and (max-width: 425px) {
    .formLogin {
        width: 70%; /* При уменьшении ширины экрана устанавливаем ширину до 80% */
    }
}

.logo{
    align-self: flex-start;
    margin-top: 4%;
    margin-left: 10%;
}

.centerBlock{
    width: 100%;
    height: 70%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.createAcc{
    margin-top: 1%;
}