.App {
  text-align: center;
}

@font-face {
  font-family: "Asap";
  src: url("../fonts/Asap/static/Asap-Medium.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: "AsapThin";
  src: url("../fonts/Asap/static/Asap-Thin.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

body, html{
  margin: 0;
  padding: 0;
}

body{
  background: var(--F4F2EE, #F4F2EE);
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  font-size: calc(10px + 5 * (100vw / 1280));
  font-family: 'AsapThin', sans-serif; /* Используйте имя шрифта, указанное в @font-face */
}

h2{
  font-family: "Asap", sans-serif;
  font-size: calc(20px + 16 * (100vw / 1280));
}

.link{
  color: #0966C2;
  cursor: pointer;
}